import './App.css';
import { useEffect, useState } from 'react';
import axios from 'axios';
import { Button, Card, Grid, Group, Image, Paper, Select, Slider, Title } from '@mantine/core';


function App() {
  const [pasta, setPasta] = useState('');
  const [subpasta, setSubpasta] = useState('');
  const [pastas, setPastas] = useState([]);
  const [imagens, setImagens] = useState([]);

  const [imgWidth, setImgWidth] = useState(150);



  useEffect(() => {
    buscarPastas()
  }, []);

  const buscarPastas = async () => {

    axios.get(`http://${window.location.hostname}/api/v1/model/pastas`, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then((response) => {
        setPastas(response.data);
      })
      .catch((error) => {
        console.log(error);
      });

  }

  useEffect(() => {

    if (pasta !== '' && subpasta !== '') {

      buscarImagens()
    }

  }, [pasta, subpasta]);


  const buscarImagens = () => {

    axios.get(`http://${window.location.hostname}/api/v1/model/classificacao`, {
      params: {
        pasta: pasta,
        subpasta: subpasta
      },
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then((response) => {
        setImagens(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  const alterarSituacaoImagem = (imagem) => {

    setImagens(imagens.map(itemImg => {

      if (itemImg.localizacao === imagem.localizacao) {

        let selecionado = !itemImg.selecionado;

        itemImg.selecionado = selecionado;
      }

      return itemImg;
    })
    )
  }

  const selecionarTudo = () => {

    setImagens(imagens.map(itemImg => {
      itemImg.selecionado = true;

      return itemImg;
    })
    )
  }

  const reclassificar = (pastaInserir) => {

    if (imagens != null) {
      let arquivos = [];

      imagens.forEach(image => {
        if (image.selecionado) {
          arquivos.push(image.localizacao)
        }
      });

      const dados = {
        pastaClassificar: pastaInserir,
        imagens: arquivos
      };

      axios.post(`http://${window.location.hostname}/api/v1/model/reclassificar/${pasta}`, dados, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then((response) => {
          buscarImagens();
        })
        .catch((error) => {
          console.log(error);
        });
    }

  }

  const treinarRede = () => {

    axios.post(`http://${window.location.hostname}/api/v1/model/treinar/${pasta}/epochs/250`, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then((response) => {
        console.log(response)
      })
      .catch((error) => {
        console.log(error);
      });
  }

  const download = () => {

    axios.get(`http://${window.location.hostname}/api/v1/model/images`, {
      params: {
        pasta: pasta,
        subpasta: subpasta
      },
      responseType: 'arraybuffer'
    })
      .then((response) => {
        const blob = new Blob([response.data], { type: 'application/zip' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'pasta.zip';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  const LabelPosicionamento = ({ posicionamento, index }) => {
    const scale = imgWidth / 512;

    const top = ((posicionamento[1] - (posicionamento[2] / 2)) * scale);
    const left = ((posicionamento[0] - (posicionamento[3] / 2)) * scale);
    const width = (posicionamento[2] * scale);
    const height = (posicionamento[3] * scale);

    return (
      <div key={index} draggable="true" style={{ position: 'absolute', marginTop: 20, marginLeft: 5, opacity: 0.5, top: top, left: left, width: width, height: height, backgroundColor: 'red' }}>

      </div>
    );
  }
  return (
    <div className="App">
      <Grid>
        <Grid.Col span={6}>
          <Select
            placeholder="Selecione uma pasta"
            data={pastas.map(itemPasta => { return { value: itemPasta, label: itemPasta } })}
            onChange={value => setPasta(value)}
          />
        </Grid.Col>
        <Grid.Col span={6}>
          <Select
            placeholder="Selecione uma SubPasta"
            data={[
              { value: 'classificados', label: 'Classificados' },
              { value: 'validos', label: 'Válidos' },
              { value: 'invalidos', label: 'Inválidos' },
              { value: './', label: 'Sem Classificação' }
            ]}
            onChange={value => setSubpasta(value)}
          />
        </Grid.Col>
        <Grid.Col span={12}>
          <Paper p="md" withBorder >
            <Grid>
              <Grid.Col span={2}>
                <Slider
                  defaultValue={imgWidth}
                  min={50}
                  max={512}
                  step={25}
                  onChange={setImgWidth}
                />
              </Grid.Col>
              <Grid.Col span={4}>
                <Group position="center">
                  <Button variant='filled' disabled={subpasta === '' || pasta === ''} onClick={treinarRede}>Treinar rede Neural</Button>
                  <Button variant='filled' disabled={subpasta === '' || pasta === ''} onClick={selecionarTudo}>Selecionar Tudo</Button>
                  <Button variant='filled' disabled={subpasta === '' || pasta === ''} onClick={download}>Download</Button>
                </Group>
              </Grid.Col>
              <Grid.Col span={6}>
                <Group position="right">
                  <Button variant='filled' disabled={subpasta === '' || subpasta === 'validos'} onClick={e => reclassificar('validos')}>Validos</Button>
                  <Button variant='filled' disabled={subpasta === '' || subpasta === 'invalidos'} onClick={e => reclassificar('invalidos')}>Invalidos</Button>
                  <Button variant='filled' disabled={subpasta === '' || subpasta === 'classificados'} onClick={e => reclassificar('classificados')}>Classificados</Button>
                  <Button variant='filled' disabled={subpasta === '' || subpasta === './'} onClick={e => reclassificar('./')}>Sem classificação</Button>
                </Group>
              </Grid.Col>
            </Grid>
          </Paper>
        </Grid.Col>
        <Grid.Col span={12}>
          <Paper p="md" withBorder>
            <Title>{subpasta}</Title>
            <Card style={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap' }}>
              {
                imagens.map((imagem, index) => (
                  <div style={{ position: 'relative' }}>
                    <Image
                      mt='md'
                      key={`${pasta}-${index}`}
                      width={imgWidth}
                      style={{ borderWidth: 5, borderRadius: 15, borderColor: imagem.selecionado === true ? '#0081bd' : 'transparent', borderStyle: 'solid' }}
                      mx="auto" radius="md"
                      src={`data:image/png;base64,${imagem.conteudo}`}
                      onClick={e => alterarSituacaoImagem(imagem, pasta)}
                      alt={imagem.localizacao} />
                    {imagem.labelImg ? imagem.labelImg.map((posicionamento, index) => <LabelPosicionamento posicionamento={posicionamento} index={index} />) : null}
                  </div>
                ))
              }
            </Card>
          </Paper>
        </Grid.Col>
      </Grid>
    </div >
  );
}

export default App;
